.app-header {
  background: var(--netflix-dark);
  padding: 40px 0;
  text-align: center;
  position: relative;
  overflow: hidden;
  border: 5px solid var(--netflix-red);
  margin-left: 5%;
  margin-right: 5%;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 3px;
  margin-bottom: 450px;
}

.app-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  opacity: 0.1;
}

.header-content {
  position: relative;
  z-index: 2;
}

.app-header h1 {
  font-size: 3.5rem;
  font-weight: 350;
  margin-bottom: 10px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.app-header p {
  font-size: 1.4rem;
  font-weight: 400;
  margin-bottom: 20px;
  opacity: 0.9;
}

.header-links {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-links a {
  color: var(--netflix-red);
  text-decoration: none;
  font-weight: 500;
  font-size: 1.1rem;
  padding: 10px 20px;
  border: 2px solid var(--netflix-red);
  border-radius: 25px;
  transition: all 0.3s ease;
  display: inline-block;
}

.header-links a:hover {
  background-color: var(--netflix-white);
  color: var(--netflix-red);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.main-content {
  padding: 40px 0;
}

.hero-section {
  text-align: center;
  margin-bottom: 10px;
  padding: 0 20px;
}

.hero-section h2 {
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 10px;
  color: var(--netflix-white);
}

.hero-section p {
  font-size: 1.2rem;
  color: var(--netflix-light-gray);
  max-width: 600px;
  margin: 0 auto;
}

/* About Me Section Styling */
.about-me {
  margin: 40px 5%;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: visible;
  min-height: 1400px;
}

.tech-stack-title {
    position: absolute;
  top: -50px;
  left: 40%;
  transform: translateX(-50%);
  color: var(--netflix-red);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  font-size: 2.5rem;
  font-weight: 600;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  z-index: 20;
}

.about-title {
  position: absolute;
  top: -50px;
  left: 46.75%;
  transform: translateX(-50%);
  color: var(--netflix-red);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  font-size: 2.5rem;
  font-weight: 600;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  z-index: 20;
}

/* Floating Sections */
.floating-section {
  position: absolute;
  z-index: 10;
  transition: transform 0.1s ease-out;
}

/* Profile Section - Top Left */
.profile-section {
  top: 250px;
  left: 10px;
  transform: translateZ(0);
  filter: drop-shadow(0 10px 25px rgba(0, 0, 0, 0.4)) 
          drop-shadow(0 4px 12px rgba(229, 9, 20, 0.2));
}

/* Text Cards */
.text-card {
  background: rgba(229, 9, 20, 0.1);
  border: 1px solid rgba(229, 9, 20, 0.3);
  border-radius: 12px;
  padding: 20px;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  max-width: 650px;
}

.text-card p {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--netflix-hover);
  margin: 0;
}

/* Tech Cards */
.tech-card {
  background: rgba(229, 9, 20, 0.1);
  border: 1px solid rgba(229, 9, 20, 0.3);
  border-radius: 12px;
  padding: 20px;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  max-width: 450px;
}

.achievement-item {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 15px;

  padding: 8px 0;
  border-bottom: 1px solid rgba(229, 9, 20, 0.2);
}

.achievement-item:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

.achievement-item i {
  font-size: 1.5rem;
  color: var(--netflix-red);
  min-width: 24px;
}

.achievement-item span {
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.4;
  color: var(--netflix-red);

}

  /* Achievements Section - Left of Intro */
  .achievements-section {
    top: 200px;
    left: 20%;
    transform: translateZ(0);
    filter: drop-shadow(0 12px 28px rgba(0, 0, 0, 0.35)) 
            drop-shadow(0 6px 15px rgba(229, 9, 20, 0.25));
  }

  /* Intro Text - Top Right */
  .intro-text {
    top: 55px;
    left: 58%;
    transform: translateZ(0);
  }

/* Mission Text - Middle Left */
.mission-text {
  top: 1050px;
  left: 5%;

  transform: translateZ(0);
}

/* Tech Group - Horizontal Layout */
.tech-group {
  top: 450px;
  left: 50%;
  transform: translateX(-50%) translateZ(0);
  filter: drop-shadow(0 15px 35px rgba(0, 0, 0, 0.4)) 
          drop-shadow(0 8px 20px rgba(229, 9, 20, 0.3));
}

.tech-group-container {
  margin-top: 100px;
  display: flex;
  gap: 30px;
  align-items: flex-start;
  position: relative;
}

/* Individual tech cards for sequential parallax */
.frontend-tech-card,
.backend-tech-card,
.devops-tech-card {
  position: relative;
  flex: 1;
  min-width: 300px;
}

.tech-group .tech-card {
  flex: 1;
  min-width: 300px;
}

.about-me::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(229, 9, 20, 0.05) 0%, rgba(229, 9, 20, 0.02) 100%);
  pointer-events: none;
}

.image-container {
  width: 300px;
  height: 300px;
  display: flex;
  justify-content: center;
  margin: 20px 0;
  overflow: visible;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border: 4px solid var(--netflix-red);
  border-radius: 50%;
  transform: scale(0.8);
}

.about-me img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  z-index: 2;
}

.about-me img:hover {
  transform: scale(1.05);
  box-shadow: 0 12px 35px rgba(229, 9, 20, 0.5);
}

.image-container:hover {
  transform: scale(0.85);
}

/* Remove independent floating animations - sections will move with parallax scroll only */

.about-me p {
  font-size: 1.1rem;
  line-height: 1.7;
  color: var(--netflix-hover);
  margin-bottom: 20px;
  max-width: 800px;
  position: relative;
  z-index: 2;
}

.about-me p:last-of-type {
  font-weight: 600;
  color: var(--netflix-red);
  font-size: 1.2rem;
  margin-top: 25px;
  margin-bottom: 15px;
}

/* Tech Categories Styling */
.tech-categories {
  margin-top: 20px;
  position: relative;
  z-index: 2;
}

.tech-category {
  margin-bottom: 30px;
}

.category-title {
  color: var(--netflix-red);
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 15px;
  text-align: center;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

/* Tech Icons Styling */
.tech-icons {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(70px, 1fr));
  gap: 15px;
  position: relative;
  z-index: 2;
}

.tech-icon {
  background: rgba(229, 9, 20, 0.1);
  border: 2px solid rgba(229, 9, 20, 0.3);
  border-radius: 12px;
  padding: 12px 8px;
  text-align: center;
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  min-height: 85px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

.tech-icon i {
  font-size: 2rem;
  color: var(--netflix-white);
  transition: all 0.3s ease;
}

.tech-label {
  font-size: 0.7rem;
  color: var(--netflix-light-gray);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.3px;
  line-height: 1;
  transition: all 0.3s ease;
}

.tech-icon:hover {
  background: rgba(229, 9, 20, 0.2);
  border-color: var(--netflix-red);
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 8px 20px rgba(229, 9, 20, 0.3);
}

.tech-icon:hover i {
  color: var(--netflix-red);
  transform: scale(1.1);
}

.tech-icon:hover .tech-label {
  color: var(--netflix-white);
}

.tech-icon::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(229, 9, 20, 0.05) 0%, rgba(229, 9, 20, 0.02) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.tech-icon:hover::before {
  opacity: 1;
}

.app-footer {
  background-color: var(--netflix-gray);
  padding: 30px 0;
  text-align: center;
  margin-top: 40px;
  border-top: 1px solid #444;
}

.app-footer p {
  color: var(--netflix-light-gray);
  font-size: 0.9rem;
}

@media (max-width: 768px) {
  .app-header h1 {
    font-size: 2.5rem;
  }
  
  .app-header p {
    font-size: 1.1rem;
  }
  
  .hero-section h2 {
    font-size: 2rem;
  }
  
  .hero-section p {
    font-size: 1rem;
  }

  /* About Me Section Mobile Styles */
  .about-me {
    margin: 20px 3%;
    padding: 30px 20px;
    min-height: 1600px;
  }

  /* Stack sections vertically on mobile */
  .floating-section {
    position: relative !important;
    top: auto !important;
    left: auto !important;
    right: auto !important;
    transform: none !important;
    margin-bottom: 30px;
  }
  
  /* Disable sequential parallax for tech cards on mobile */
  .frontend-tech-card,
  .backend-tech-card,
  .devops-tech-card {
    position: relative !important;
    transform: none !important;
  }
  
  /* Disable shadows on mobile for performance */
  .profile-section,
  .achievements-section,
  .tech-group {
    filter: none !important;
  }

  .profile-section {
    order: 1;
  }

  .achievements-section {
    order: 2;
  }

  .intro-text {
    order: 3;
  }

  .mission-text {
    order: 4;
  }

  .tech-group {
    order: 5;
  }

  .tech-group-container {
    flex-direction: column;
    gap: 20px;
  }

  .tech-group .tech-card {
    min-width: 100%;
  }

  .image-container {
    width: 250px;
    height: 250px;
    margin: 0 auto;
  }

  .text-card, .tech-card {
    max-width: 100%;
  }

  .about-me p {
    font-size: 1rem;
    line-height: 1.6;
    margin-bottom: 18px;
  }

  .about-me p:last-of-type {
    font-size: 1.1rem;
  }

  .about-title {
    font-size: 2rem;
  }

  .category-title {
    font-size: 1rem;
  }

  .tech-icons {
    grid-template-columns: repeat(auto-fit, minmax(60px, 1fr));
    gap: 12px;
  }

  .tech-icon {
    padding: 10px 6px;
    min-height: 75px;
  }

  .tech-icon i {
    font-size: 1.6rem;
  }

  .tech-label {
    font-size: 0.65rem;
  }
}

@media (max-width: 480px) {
  .about-me {
    margin: 15px 2%;
    padding: 25px 15px;
    min-height: 1500px;
  }

  .image-container {
    width: 200px;
    height: 200px;
  }

  .text-card, .tech-card {
    padding: 15px;
  }

  .about-me p {
    font-size: 0.95rem;
    line-height: 1.5;
    margin-bottom: 15px;
  }

  .about-me p:last-of-type {
    font-size: 1rem;
  }

  .about-title {
    font-size: 1.8rem;
  }

  .category-title {
    font-size: 0.9rem;
  }

  .tech-icons {
    grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
    gap: 10px;
  }

  .tech-icon {
    padding: 8px 5px;
    min-height: 70px;
  }

  .tech-icon i {
    font-size: 1.4rem;
  }

  .tech-label {
    font-size: 0.6rem;
  }
}
